<template>
   <div class='root'>
      <action-bar title='设置'></action-bar>
      <div class='container'>
         <div class='content'>
            <div class="content-item" @click="bindPrivacyAgreement">
               <div>隐私政策</div>
               <img class="img-right" src="@/assets/arrow_right_icon.png" />
            </div>
         </div>
      </div>
      <div class="operation">
         <div class="button-normal login-out" @click="bindLoginOut">
            退出当前账户
         </div>
      </div>
   </div>
</template>
<script>
import { Dialog } from 'vant'
import { exit } from '@/http/api'
export default {
  name: 'managerSetting',
  components: {

  },
  data () {
    return {
    }
  },
  methods: {
    bindPrivacyAgreement () {
      window.open(this.$store.state.baseUrl + '/static/notice/supplier_privacy.html')
    },
    bindLoginOut () {
      Dialog.confirm({
        message: '确定要退出吗？',
        confirmButtonColor: '#ff0000'
      })
        .then(() => {
          // on confirm
          this.loginOut()
        })
        .catch(() => {
          // on cancel
        })
    },
    loginOut () {
      exit().then(res => {
        this.$store.commit('setDeviceId', '')
        this.$store.commit('updateCloseShop', '')
        this.$router.replace('/login')
      }).catch(res => {
        this.$toast(res.msg)
      })
    }
  },
  mounted () {
  }
}
</script>
<style lang="scss" scoped>
.root {
   width: 100vw;
   height: 100vh;
   display: flex;
   flex-direction: column;
}

.container {
   flex-grow: 1;
   position: relative;
}

.content {
   position: absolute;
   left: 0;
   right: 0;
   top: 0;
   bottom: 0;
}

.img-right {
   width: .4375rem;
   height: auto;
}

.content-item {
   padding: .8125rem;
   display: flex;
   background: white;
   justify-content: space-between;
   align-items: center;
   border-bottom: .125rem solid #F2F2F2;
   font-size: .875rem;
}

.operation{
  width: 100%;
  height: 3.75rem;
  padding: .5625rem;
  box-sizing: border-box;
}

.login-out{
  height: 100%;
  font-size: 1rem;
  border-radius: .375rem;
}

.button-normal {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: #42D18A;
  border-radius: .25rem;
  font-weight: 500;
}
</style>
